<template>
	<Example id="example-table-sorter">
		<template slot="source">
			<vui-table v-bind:columns="columns" v-bind:data="data" rowKey="id">
				<vui-action-group slot="action" slot-scope="{ row, rowIndex }">
					<a href="javascript:;">Edit</a>
					<a href="javascript:;">Delete</a>
				</vui-action-group>
			</vui-table>
		</template>
		<template slot="title"></template>
		<template slot="describe">
			<p> <code>columns</code>  <code>sorter</code> </p>
			<p> <code>sorter.method</code>  <code>a</code><code>b</code>  <code>order</code>  <code>a</code><code>b</code> <code>order</code> </p>
			<p> <code>sorter.order</code> </p>
			<p> <code>sorter.useServerSort</code>  <code>true</code> <code>filter</code>  <code>data</code> </p>
			<p> <code>data</code></p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import code from "./code";

	export default {
		components: {
			Example
		},
		data() {
			const columns = [
				{
					key: "name",
					dataIndex: "name",
					width: 200,
					title: "Name",
					sorter: {
						method(a, b, order) {
							if (order === "asc") {
								return a.length > b.length ? 1 : -1;
							}
							else if (order === "desc") {
								return a.length < a.length ? 1 : -1;
							}
							else {
								return 0;
							}
						}
					}
				},
				{
					key: "age",
					dataIndex: "age",
					width: 140,
					sorter: true,
					title: "Age"
				},
				{
					key: "address",
					dataIndex: "address",
					title: "Address"
				},
				{
					key: "action",
					title: "Action",
					slot: "action",
					width: 140
				}
			];

			const data = [
				{ id: 1, name: "John Brown", age: 20, address: "New York No. 1 Lake Park" },
				{ id: 2, name: "Jim Green", age: 24, address: "London No. 1 Lake Park" },
				{ id: 3, name: "Joe Black", age: 30, address: "Sidney No. 1 Lake Park" },
				{ id: 4, name: "Jon Snow", age: 26, address: "Ottawa No. 2 Lake Park" }
			];

			return {
				code,
				columns,
				data
			};
		}
	};
</script>


